import Accordion from "react-bootstrap/Accordion";

function AlwaysOpenExample2() {
    const message =
        " 1.  1 \n 2.     Account  \n 3.  Chiangmaiarea1 Account  name@chiangmaiarea1.go.th ";
    const message2 =
        " 1.  Email  Google \n 2.   \n 3.  \n 4.     ";

    return (
        <Accordion>
            <div className="FAQ_text">
                <b> Chiangmaiarea1 Account</b>
            </div>
            <Accordion.Item eventKey="0">
                <Accordion.Header> Chiangmaiarea1 Account  </Accordion.Header>
                <Accordion.Body>
                    <div className="FAQ_text">
                        <span className="css-fix">{message}</span>
                    </div>
                </Accordion.Body>
            </Accordion.Item>
            <Accordion.Item eventKey="1">
                <Accordion.Header> Username  Password </Accordion.Header>
                <Accordion.Body>
                    <div className="FAQ_text">
                        <span className="css-fix">{message2}</span>
                    </div>
                </Accordion.Body>
            </Accordion.Item>
        </Accordion>
    );
}

export default AlwaysOpenExample2;
